<template>
  <main v-if="orderInfo"
        class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link"
                       :to="{ name: 'main' }"
          >
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link"
                       :to="{ name: 'cart' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{ $route.params.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
                .        .
                       .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                  
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order"
                v-for="product in productsOrder"
                :key="product.id"
            >
              <h3>{{ product.product.title }}</h3>
              <b>{{product.product.price | numberFormat }}</b>
              <span>
                : {{ product.product.id }}
              </span>
              <span>
                  {{ product.quantity }} .
              </span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>{{ Number(priceDelivery) }} </b></p>
            <p>: <b>{{ countProduct }}</b>
                 <b>{{ totalPriceDelivery | numberFormat }} </b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>

import { mapGetters, mapState } from 'vuex';
import numberFormat from '@/helpers/numberFormat';

export default {
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({
      totalPrice: 'orderTotalPrice',
      countOrderProducts: 'orderCountProduct',
    }),
    ...mapState({
      orderInfo: (state) => state.orderInfo,
      priceDelivery: (state) => state.priceDelivery,
    }),
    countProduct() {
      return this.countOrderProducts;
    },
    totalPriceDelivery() {
      return this.totalPrice + Number(this.priceDelivery);
    },
    productsOrder() {
      return this.orderInfo.basket.items;
    },
  },
  created() {
    if (this.orderInfo && this.orderInfo.id === this.$route.params.id) {
      return;
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
};
</script>
